export default "canvas {\n    width: 200px;\n    height: 200px;\n    border: 1px solid black;\n}\n\n.grab[draggable=true] {\n    cursor: move; /* fallback if grab cursor is unsupported */\n    cursor: grab;\n    cursor: -moz-grab;\n    cursor: -webkit-grab;\n}\n\n /* (Optional) Apply a \"closed-hand\" cursor during drag operation. */\n.grab:active[draggable=true] {\n    cursor: grabbing;\n    cursor: -moz-grabbing;\n    cursor: -webkit-grabbing;\n}"