export default ".monster {\n    height: 100%;\n    cursor: grab;\n    position: relative;\n    width: 100%;\n    display: inline-block;\n}\n\n.monster + .stats{\n    visibility: hidden;\n    width: 200px;\n    border: 1px black solid;\n    text-align: center;\n    background-color: white;\n    padding: 5px 0;\n    box-shadow: 3px 7px 5px 0px rgba(0,0,0,0.75);\n    z-index: 1;\n    position:relative;\n    bottom: 350%;\n    left: 35%;\n    \n    margin-left: -60px;\n}\n\n\n\n\n.monster:hover:not(:active) + .stats{\n    visibility: visible;\n}\n"