export default ".panel{\n\n}\n\ng-form {\n    min-width: 230px;\n}\n\n\n\n.base {\n    position: absolute;\n    display: grid;\n    grid-template-columns: 100px 50px 100px;\n    grid-template-rows: 80px auto 80px;\n    grid-column-gap: 10px;\n    grid-row-gap: 15px;\n    width: 1000px;\n    height: 1000px;\n\n}\n\n\n.jungle g-element {\n    background-color: rgba(0, 255, 1, 0.2);\n}\n\n.sjahari g-element {\n    background-color: rgba(255, 241, 9, 0.2);\n}\n\n.icepole g-element {\n    background-color: rgba(71, 225, 255, 0.2);\n}\n\ndiv {\n    /* background-color: rgb(100, 1, 1); */\n    height: 100%;\n    width: 100%;\n}\n\ng-element {\n    position: absolute;\n    height: 100px;\n    width: 100px;\n    border: solid black 0.5px;\n}\n\n\n\n.rock {\n    background-color:black;\n}\n"